
.wa_top{
    width:100%;
    height:6.46rem;
    background: url("http://lianai-image-sys.qiniudn.com/wa/bg.png") no-repeat center center;
    background-size: 87% 100%;
    position: relative;
    padding-top: 2.08rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
}
.name{
    color:#222;
    font-size: 0.32rem;
    margin-top:0.2rem;
}
.id{
    font-size: 0.2rem;
    color:#666;
    opacity: 0.5;
}
.wa_top>span{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:6.46rem;
    background: url("http://lianai-image-sys.qiniudn.com/wa/s_top.png") no-repeat center center;
    background-size: 100% 100%;
}
.number{
    position: absolute;
    bottom:0.3rem;
    left:0;
    text-align: center;
    width:100%;
    height:auto;
    font-size: 0.34rem;
}
.number>em{
    font-size: 0.58rem;
    color:#fff;
    margin:0 0.05rem;
    text-shadow: 0 1px #000, 1px 0 #000, -1px 0 #000, 0 -1px #000;
}
.down{
    width:3.8rem;
    height:0.56rem;
    margin:0.32rem auto 0.56rem;
}
.head{
    width:2rem;
    height:2rem;
    border-radius: 50%;
    margin:0 auto;
}
.waLists{
    width:100%;
    height:auto;
    padding:0 0.2rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-bottom: 2.58rem;
   /* background: #fff203;*/
}
.waLists>li{
    width:49%;
    float: left;
    height:4.58rem;
    border-radius: 0.16rem;
    background: #fff;
    margin-top:0.14rem;
}
.waLists>li:nth-of-type(even){
    float: right;
}
.waLists>li>div{
    width:100%;
    height:3.46rem;
    text-align:center;
}
.waLists>li>div>img{
    width:100%;
    height:100%;
    border-top-left-radius: 0.16rem;
    border-top-right-radius: 0.16rem;
}
.pic_name{
    margin-top:0.24rem;
}
.pic_name,.pic_time{
    font-size: 0.28rem;
    color: #333;
    padding:0 0.16rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-weight: 700;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.pic_time{
    font-size: 0.24rem;
    color:#999;
    font-weight:normal;
}
.r_footer{
    position: fixed;
    bottom:0;
}
.code_i{
    margin-top:0.1rem;
    color:#000;
}
